




















import { defineComponent, ref } from '@vue/composition-api';
import UploadHomePageImg from '@/views/supplier-backend/components/UploadHomePageImg.vue';
import { i18n } from '@/i18n';

interface Link {
  type: 0 | 1 | 2 | 3 | 4 | 5,
}

const Picture = defineComponent({
  name: 'Picture',
  components: {
  },
  props: {
    editable: {
      type: Boolean,
      default: false,
    },

    link: {
      type: Object as () => Link,
      default: null,
    },
    image: {
      type: String,
    },
  },
  setup(props, { emit, root: { $injectDialog, $route } }) {
    let imgDefault = '';
    let hoverTitle = '';
    let hoverTitleSub = '';
    const materialQuery = { ...$route.query };

    const linkType = props.link?.type;

    let maxSize = 2 * 1024 * 1024;
    let sizeTips = i18n.t('supplier-backend.homepage.upload_img_tip', ['564*564px', '2MB']);

    if (linkType === 1) {
      imgDefault = require('@/assets/img/material-home/new-default.png');
      materialQuery.orderBy = 'gmtStoreTime';
      materialQuery.asc = 'false';
      materialQuery.size = '50';
      materialQuery.pageName = i18n.t('material-mall.page_name.new');
      hoverTitle = 'NEW';
      hoverTitleSub = i18n.language === 'cn' ? '新品面料' : '';
    } else if (linkType === 2) {
      imgDefault = require('@/assets/img/material-home/hot-default.png');
      materialQuery.orderBy = 'click';
      materialQuery.asc = 'false';
      materialQuery.size = '50';
      materialQuery.pageName = i18n.t('material-mall.page_name.most_viewed');
      hoverTitle = 'MOST VIEWED';
      hoverTitleSub = i18n.language === 'cn' ? '人气面料' : '';
    } else if (linkType === 3) {
      imgDefault = require('@/assets/img/material-home/staff-picks-default.png');
      materialQuery.keyword = '推荐|Staff Picks';
      materialQuery.pageName = i18n.t('material-mall.page_name.staff_picks');
      hoverTitle = 'STAFF PICKS';
      hoverTitleSub = i18n.language === 'cn' ? '推荐面料' : '';
    } else if (linkType === 4) {
      imgDefault = i18n.language === 'cn' ? require('@/assets/img/material-home/core-collection-default.png')
        : require('@/assets/img/material-home/core-collection-default-en.png');
      materialQuery.keyword = '主营|Core Collection';
      materialQuery.pageName = i18n.t('material-mall.page_name.core_collection');
      maxSize = 4 * 1024 * 1024;
      sizeTips = i18n.t('supplier-backend.homepage.upload_img_tip', ['1740*700px', '4MB']);
    } else if (linkType === 5) {
      imgDefault = i18n.language === 'cn' ? require('@/assets/img/material-home/in-season-default.png')
        : require('@/assets/img/material-home/in-season-default-en.png');
      materialQuery.keyword = '当季|In Season';
      materialQuery.pageName = i18n.t('material-mall.page_name.in_season');
      maxSize = 4 * 1024 * 1024;
      sizeTips = i18n.t('supplier-backend.homepage.upload_img_tip', ['1740*700px', '4MB']);
    } else {
      imgDefault = i18n.language === 'cn' ? require('@/assets/img/material-home/banner-default.png')
        : require('@/assets/img/material-home/banner-default-en.png');
      materialQuery.pageName = i18n.t('material-mall.page_name.all');
      maxSize = 4 * 1024 * 1024;
      sizeTips = i18n.t('supplier-backend.homepage.upload_img_tip', ['1740*830px', '4MB']);
    }

    async function showUpload() {
      emit('edit', true);
      try {
        const { field, value } = (await $injectDialog({
          is: UploadHomePageImg,
          props: {
            url: props.image || imgDefault,
            maxSize,
            sizeTips,
          },
        }) as { field: string, value: string });
        emit('change', { path: ['image'], value });
      } finally {
        emit('edit', false);
      }
    }
    return {
      // changeBanner,
      showUpload,
      imgDefault,
      materialQuery,
      hoverTitle,
      hoverTitleSub,
    };
  },
});
export default Picture;
